import React from 'react';

import './Home.css';
import border_blue from '../../../img/border_blue.svg';

const Home = ({setDialogText, setCurrentAction}) => {

    return (
        <div id="home">
            <h1>Welcome</h1>

            <div className="welcome">
                <p>To navigate the website, please interact with my avatar.</p>
            </div>

            <div className="welcome secondary">
                <h2>Shortcuts</h2>
                <button
                className='ui button blue'
                style={{borderImageSource: `url(${border_blue})`}}
                onClick={()=>{
                    setCurrentAction("goTo-portfolio");
                    setDialogText("Right to the point, huh? I like that!");
                }}>
                    Check Out My Portfolio
                </button>
                <a href="https://maw.dev/cv/CV-MauricioGarciaDeCeca_EN.pdf" title="Maw Resume" rel="noreferrer" target="_blank">
                    <button
                    className="ui button blue"
                    style={{borderImageSource: `url(${border_blue})`}}>
                        Check Out My Resume
                    </button>
                </a>
                <button
                className='ui button blue'
                style={{borderImageSource: `url(${border_blue})`}}
                onClick={()=>{
                    setCurrentAction("goTo-contact");
                    setDialogText("I'd like to hear from you!");
                }}>
                    Contact Me!
                </button>
            </div>
            
        </div>
    )
}

export default Home;